<template>
	<div>
		<div @click="ss" v-for="item in bolist">
			<a class="box-list" :href="'/pages/exhibition/main?id='+item.id">
				<div class="box-img">
					<img class="img-test" :src='item.coverUrl' />
				</div>
				<div class="box-introduce">
					<div class="enen" v-text="item.name"></div>
					<div class="box-key-list">
						作者：<span v-text="item.author"></span>
					</div>
					<div class="box-key-list">出版社：<span v-text="item.press"></span></div>
					<div class="box-key-list">状态：
						<icon v-if='item.status == 1' type="success" size="20"></icon>
						<icon v-if='item.status == 0' type="warn" size="20"></icon>
					</div>
				</div>
			</a>
		</div>
	</div>
</template>

<script>
	var Fly = require("flyio/dist/npm/wx")
	var fly = new Fly
	export default {
		props: ['bolist'],
		//注册组件
		components: {},
		//数据
		data() {
			return {
				isPersonalShow: false,
				o: 0,
				imurl: 'https://diycode.b0.upaiyun.com/photo/2017/fc7727985fd40e43f6b5bd6192dc23b7.jpeg',
				bookmsg: [],
				s: ''

			}
		},
		//组件创建时
		created() {
		},
		//数据挂载时
		mounted() {},
		//计算属性
		computed: {},
		//方法
		methods: {
			ss() {
				this.o = 123;
			}
		},
		//观测方法
		watch: {},

	};
</script>

<style>
	.box-list {
		display: -webkit-box;
		width: 750rpx;
	}
	
	.box-img {
		width: 120rpx;
		-webkit-box-flex: 1.1;
		height: 240rpx;
	}
	
	.box-introduce {
		-webkit-box-flex: 1.9;
		height: 240rpx;
		display: box;
		box-orient: horizontal;
		border-bottom-style: outset
	}
	
	.box-key-list {
		margin-top: 3rpx;
		margin-left: 40rpx;
		-webkit-box-flex: 1.0;
		font-size: 30rpx;
	}
	
	.enen {
		font-weight: bold;
		margin-left: 40rpx;
		margin-top: 10rpx;
		margin-bottom: 20rpx;
	}
	
	.img-test {
		margin-left: 8rpx;
		margin-top: 20rpx;
		width: 200rpx;
		height: 200rpx;
	}
</style>


