<template>
	<div class="pagec">
		<div>
			<!--<headers v-on:listsssss="getBookByName"></headers>-->
			<div>
				请输入:
				<input v-model="name" type="text" placeholder="请输入书名" />
				<button class="weui-btn mini-btn" type="primary" size="mini" @click="sendBookName">按钮</button>
			</div>
		</div>
		<div>
			<booklist :bolist="bookmsg"></booklist>
		</div>
	</div>
</template>

<script>
	import header from '@/components/header'
	import booklist from '@/components/booklist'
	var Fly = require("flyio/dist/npm/wx")
	var fly = new Fly
	export default {
		components: {
			headers: 'header',
			booklist,
		},
		data() {
			return {
				logs: [],
				bookmsg: [],
				name:'',
			}
		},
		methods: {
			sendBookName() {
				fly.get('http://39.106.221.38:8080/clw-ser/findByName?bookName='+this.name)
				.then((response) => {
					this.bookmsg = response.data.BookList;
					this.name = '';
				})
			}
		},
		created() {
			fly.get('http://39.106.221.38:8080/clw-ser/findByName?bookName='+this.name)
				.then((response) => {
					this.bookmsg = response.data.BookList;
				})
		},

	}
</script>

<style>
	.pagec {
		box-orient: horizontal;
	}
</style>


